<template>
  <div class="row">
    <form @submit.prevent="onSubmit" class="col l6 m8 s12 offset-l3 offset-m2 offset-s0">
      <div class="row">
        <h5>4. Прочее</h5>
      </div>
      <div class="data-list">
        <label>
          <input type="checkbox" class="filled-in" v-model="harm" />
          <span>Вредность</span>
        </label>
      </div>
      <div class="overtime">
        <label>
          <input type="checkbox" class="filled-in" v-model="eatChecked" />
          <span>Обедал в столовой</span>
        </label>
         <div class="inputs-list" v-show="eatChecked">
          <InputWithButtons
            id="eats"
            placeholder="Сколько раз"
            v-model="eat"
            :required="true"
          />
        </div>

       </div>

      <ControlButtons linkBack="/overtime" nextText="Следующий шаг" />
    </form>
  </div>
</template>

<script>
import ControlButtons from "@/components/ControlButtons";
import InputWithButtons from "@/components/InputWithButtons";

export default {
  name: "other",
  components: { ControlButtons, InputWithButtons },
  data: () => ({
    harm: false,
    eat: 1,
    eatChecked: false
  }),
  mounted() {
    M.updateTextFields();
  },
  methods: {
    changeEat(type) {
      if (type === "down") this.eat--;
      else this.eat++;
    },
     onSubmit(){
      let eat = this.eatChecked ? this.eat : 0
      let other = {
        harm: this.harm,
        eat
      }
      this.$store.dispatch('setOther', other)
      this.$router.push("/final");
    }
  }
};
</script>

<style lang='scss' scoped>
label {
   display: block;
   margin-bottom: 1rem;
}
.btn-plus {
  float: right;
}
.btn-count {
  margin-top: 1rem;
}
</style>